<template>
    <div id="login">
        <mt-tab-container  v-model="active">
            <div class="title">
                <a href="javascript:;" @click="loginpag">登陆</a>
                <a href="javascript:;" @click="registpag">注册</a>
            </div>
            <mt-tab-container-item id="login">
                <mt-cell>
                    <form action="">
                        <p><span>用户名</span><input type="text" v-model="logindata.username" placeholder="请输入你的用户名" ></p>
                        <p><span>密码</span><input type="password" v-model="logindata.password"></p>
                        <p><input type="button" @click="loginclick" class="text-c" value="登陆"></p>
                    </form>
                </mt-cell>
            </mt-tab-container-item>
            <mt-tab-container-item id="regist">
                <mt-cell>
                    <form action="">
                        <p><span>用户名</span><input type="text" v-model="registdata.username" placeholder="请输入你的用户名"></p>
                        <p><span>密码</span><input type="password"  v-model="registdata.password"></p>
                        <p><span>确认密码</span><input type="password"   v-model="registdata.passwords"></p>
                        <p><input type="button" class="text-c" @click="registclick" value="注册"></p>
                    </form>
                </mt-cell>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>
<script>
export default {
    name:'login',
    data(){
        return {
            active:"login",
            registdata:{
                username:"",
                password:"",
                passwords:"",
            },
            logindata:{
                username:"",
                password:"",
            }
        }
    },
    methods:{
        registpag(){
            this.active = "regist"
        },
        loginpag(){
            this.active = "login"
        },
        registclick(){

            if(this.registdata.username !== ""){
                if(this.registdata.password === this.registdata.passwords && this.registdata.password !== ""){
                localStorage.setItem("user",JSON.stringify(this.registdata))
                this.active = "login"
                alert("注册成功")
                this.registdata.username = ''
                this.registdata.password = ''
                this.registdata.passwords = ''
                }else{
                    alert("密码为空或两次密码输入不正确")
                }
            }else{
                alert("用户名为空，请重试")
            }
        },
        loginclick(){
            var user = JSON.parse(localStorage.getItem('user')) || 0
            if(user !== 0){
                if(user.password === this.logindata.password){
                    localStorage.setItem("islogin",JSON.stringify(1))
                    alert("登陆成功")
                    this.$router.push("/my")
                }else{
                    alert("密码错误，请重试")
                }
                
            }else{
                alert("用户名为空或用户未注册，请注册")
            }
        }
    }
}
</script>
<style scoped>
    #login{
        margin-top: 40px;
        background: #ffffff;
    }
    .title{
        margin-top: 10px;
        height: 50px;
        font-size: 24px;
        line-height: 50px;
        font-weight: bolder;
    }
    .title a{
        margin: 10px 20px;
        color: #000000;
    }
    form{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
    }
    span{
        width: 100px;
        font-size: 17px;
        line-height: 40px;
    }   
    p{
        display: inline-flex;
    }
    .text-c{
        padding: 10px 20px;
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 2px;
        margin-left: 40%;
    }
</style>