<template>
    <div id="my">
        
        <div class="sec" v-if="islogin === 0">
            <p>
                <router-link to="/login">登陆/注册</router-link>
            </p>
        </div>
        <div class="sec" v-else>
            <p>
                <a href="#">
                    尊敬的  {{userdata.username}}  ,你好。
                </a> 
            </p>
            <p  class="zx">
                <a href="#" @click="zxclick">注销</a>
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name:'my',
    data() {
        return {
            islogin:false,
            userdata:{}
        }
    },
    mounted(){
        this.islogin = JSON.parse(localStorage.getItem("islogin")) || 0 
        this.userdata = JSON.parse(localStorage.getItem('user'))
    },
    methods:{
        zxclick(){
                localStorage.setItem("islogin",JSON.stringify(0))
                this.islogin = false
        }
    }
}
</script>
<style scoped>
    #my{
        margin-top: 40px;
        padding: 10px 10px;
    }
    .sec{
        text-align: left;
        background: blue;
        padding: 20px;
    }
    .sec p a{
        
        font-size: 18px;
        color: #ffffff;
        letter-spacing: 2px;
        margin-right: 10px;
        font-weight: bolder;
    }
    .zx{
        text-align: right;
        line-height: 25px;
    }
</style>